import { Component, Element, h, Host, Method, Prop } from '@stencil/core';

@Component({
  tag: 'storm-horizontal-split-view',
  styleUrl: 'storm-horizontal-split-view.css',
  shadow: false,
})
export class StormHorizontalSplitView {
  private _splitViewRef: HTMLStormSplitViewElement;

  @Element() host: HTMLElement;

  @Prop() splitPercent: number = 50;
  @Prop() fixedSize?: string;
  @Prop() fixedPane?: 'top' | 'bottom';
  @Prop({ mutable: true }) topPaneVisible?: boolean;
  @Prop({ mutable: true }) bottomPaneVisible?: boolean;

  @Method()
  async viewToggle(token: 'top' | 'bottom', force: boolean) {
    await this._splitViewRef.viewToggle(token === 'top' ? 'first' : 'second', force);
  }

  componentWillLoad() {
    this.topPaneVisible = this.topPaneVisible ?? this.host.querySelector(':scope>[slot="top-pane"]') != null;
    this.bottomPaneVisible = this.bottomPaneVisible ?? this.host.querySelector(':scope>[slot="bottom-pane"]') != null;
  }

  render() {
    return (
      <Host>
        <storm-split-view
          ref={elm => (this._splitViewRef = elm)}
          splitPercent={this.splitPercent}
          fixedSize={this.fixedSize}
          fixedPane={this.fixedPane == null ? null : this.fixedPane == 'top' ? 'first' : 'second'}
          firstPaneVisible={this.topPaneVisible}
          secondPaneVisible={this.bottomPaneVisible}
          direction={'horizontal'}
          firstPaneName={'top-pane'}
          secondPaneName={'bottom-pane'}
        >
          <slot name="top-pane"></slot>
          <slot name="bottom-pane"></slot>
        </storm-split-view>
      </Host>
    );
  }
}
